import React from 'react';
import { Button } from 'react-bootstrap';
import './Card.css';

const Card = ({url_imagen, imagen_desc, ubicacion, camara, eventoClic}) => {

    const keyword_extractor = require("keyword-extractor");
    const extraction_result =
            keyword_extractor.extract(imagen_desc,{
            language:"english",
            remove_digits: true,
            return_changed_case:false,
            remove_duplicates: true
        })
        
    return(
        <div className="col-lg-4 col-md-6 col-sm-6 galeria-item">
            <img src={url_imagen} className="card-img-top" alt={imagen_desc}/>
            <h5 className="ubicacion">Ubicacion: {ubicacion?ubicacion:'Desconocida'}</h5>
            <span>Camara: {camara?camara:'No especificada'}</span>
            <div className='etiquetas'>
                {extraction_result.map(palabra =>(
                    <Button as="input" 
                        className='etiqueta-item'
                        type="button"
                        value={palabra} 
                        key={palabra} 
                        onClick={(e) => eventoClic (e.target.value)}
                        />
                ))}
            </div>
            
        </div> 
    )
}

export { Card }