import React, {useEffect, useState} from 'react';
import axios from 'axios';
import Container from 'react-bootstrap/Container';
import { Row } from 'react-bootstrap';
import "./BuscadorImages.css";
import { Card } from '../ItemGaleria/Card';

const BuscadorImages = () => {
    
    //const publicKey='_bs0Vha5tnyxZDoIZV86Q0KrLSwMzgeudyiRaJQ0f1I'
    const publicKey='tywG0ALof6y8Wdx08c0kxpfQQUAz6byG-VvWPfRlfBE'
    //const publicKey='YWCI3XPnd2SKKmQ-cHk-M514CBfIqAQyUH2BvSf3KwI'
    //const publicKey='h9FZ0xLVL_18gdNX6UbtLaaOOvTM0Y8cf4LKm1VSp88'
    const URLbase ='https://api.unsplash.com'
    const endPointFotos='/photos/random?count=30'
    
    const [imagenes, setImagenes] = useState([])
    const [input, setInput] = useState(' ')
    const [busqueda, setBusqueda] = useState(' ')
    
    useEffect(()=>{
        obtenerImagenes(busqueda)
        window.addEventListener('scroll', handleScroll)
        
        
    },[busqueda])
    
    
    const handleScroll = (e) => {  
        if (window.innerHeight + e.target.documentElement.scrollTop >= e.target.documentElement.scrollHeight){
            obtenerMasImagenes(busqueda)
            const numero =e.target.documentElement.scrollTop/1
            console.log(numero)
            window.scrollTo(0, 2000);
        }
        
    }
    // Busca la imagenes random 
    const obtenerImagenes = async (query) =>{
        const response = await axios.get(`${URLbase}${endPointFotos}&&query=${query}&&client_id=${publicKey}`)
        setImagenes(response.data)
    }
    // Guardo la busqueda del usuario
    const terminoBusqueda = (valor) => { setInput(valor) }  
    // Guardo la etiqueta cliqueada
    const clicEtiqueta = (valor) => {setBusqueda (valor)}
    // La asigno a busqueda para disparar la obtencion de imagenes
    const definirQuery = () =>{ setBusqueda(input) }

    const obtenerMasImagenes = async (query) =>{
        const response = await axios.get(`${URLbase}${endPointFotos}&&query=${query}&&client_id=${publicKey}`)
        const mas_imagenes = imagenes.concat(response.data)
        setImagenes(mas_imagenes)
        //console.log(response.data)
    }

    return (
        <section>    
            <Container className="buscador-imagenes">
                
                {/* BUSCADOR */}
                <Row className="botones">
                    <div className="col-md-5 input-icono">
                        <i className="bi bi-search"></i>
                        <input type="text" className=" " onChange={(e) =>terminoBusqueda (e.target.value)} placeholder="Buscar" id='buscador'/>
                    </div>
                    <button className=" " id='buscador' type="button" onClick={definirQuery} >Buscar</button>
                </Row>
                
                {/* GALERIA */}
                <Row className="galeria">
                    {imagenes.map(imagen => (
                        <Card
                            key={imagen.id}
                            url_imagen={imagen.urls['thumb']}
                            camara={imagen.exif['model']}
                            imagen_desc={imagen.alt_description}
                            ubicacion={imagen.location['name']}
                            eventoClic={clicEtiqueta}
                            
                            />   
                        ))}
                </Row>
            </Container>
        </section>
    );
};

export {BuscadorImages};
 