import React from 'react';
import './Footer.css';

const Footer = ({nompre_apellido, curso, trabajo_practico, fecha_fin}) => {
    return(
        <footer>
            <div className='info'>
                <div className='alumna'>
                    <h6>{nompre_apellido}</h6>
                </div>
                <div className='curso'>
                    <p>{curso}</p>
                </div>
                <div className='tp'>
                    <h6>Trabajo: </h6>
                    <p>{trabajo_practico}</p>
                </div>
                <div className='fecha'>
                    <h6>Fecha de entrega: </h6>
                    <p>{fecha_fin}</p>
                </div>
            </div>
        </footer>
    )
}

export { Footer }