import './Navbar.css';

import React from 'react';

const Navbar = ({url_github, url_linkedin}) => {
    return(
        <header>
            <h1><a href="/"> Bq.</a></h1>
            <nav className="redes">
                <a href={url_github}><span className="bi bi-github"></span></a>
                <a href={url_linkedin}><span className="bi bi-linkedin"></span></a>
            </nav>
        </header>
    )
}

export { Navbar }